.contenedor-principal{
  height: 100vh;
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  background-color: #1b1b32;
  justify-content: center;
  align-items: center;
  font-family: lato, sans-serif;
}
.calcu{
  height: 83vh;
width: 350px;
  display: flex;
  flex-wrap: wrap;
  background-color: aqua;
  border-radius: 20px;
  border: 5px white solid;
  padding: 14px;
}
.fila {
  display: flex;
  justify-items: center;
  align-items: center;
  margin: 4px;
  width: 100%;
  height: 40px;
  margin-top: 10px;
}